/* Using SCSS variables to store breakpoints */
.blog-header {
	line-height: 1;
	border-bottom: 1px solid #e5e5e5;
}
.blog-header-logo {
	font-family: "Playfair Display", Georgia, "Times New Roman", serif;
	font-size: 2.25rem;
}
.blog-header-logo:hover {
	text-decoration: none;
}
.blog-header__social-icon {
	height: 20px;
	width: auto;
}

.marquee__container {
	margin: 1rem 0;
	padding: 0.5rem;
}
.marquee__container p {
	font-weight: 700;
	margin-bottom: 0;
	text-transform: uppercase;
	font-style: italic;
}

/*
 * Footer
 */
.blog-footer {
	padding: 2.5rem 0;
	text-align: center;
	border-top: 0.05rem solid #e5e5e5;
}

.blog-footer p:last-child {
	margin-bottom: 0;
}

.depth-1 {
	margin-left: 40px;
}

.depth-2 {
	margin-left: 40px;
}

.depth-3 {
	margin-left: 40px;
}

.depth-4 {
	margin-left: 40px;
}

.depth-5 {
	margin-left: 40px;
}

.depth-6 {
	margin-left: 40px;
}

.depth-7 {
	margin-left: 40px;
}

.depth-8 {
	margin-left: 40px;
}

.depth-9 {
	margin-left: 40px;
}

.depth-10 {
	margin-left: 40px;
}

.depth-11 {
	margin-left: 40px;
}

.depth-12 {
	margin-left: 40px;
}

.depth-1 {
	margin-left: 0;
}

.crypto-modal-search {
	margin: 0;
	max-width: none;
}

.bd-placeholder-img {
	font-size: 1.125rem;
	text-anchor: middle;
	user-select: none;
}

@media (min-width: 768px) {
	.bd-placeholder-img-lg {
		font-size: 3.5rem;
	}
}
h1, h2, h3, h4, h5, h6 {
	font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.display-4 {
	font-size: 2.5rem;
}
@media (min-width: 768px) {
	.display-4 {
		font-size: 3rem;
	}
}

.nav-scroller {
	position: relative;
	z-index: 2;
	height: 2.75rem;
	overflow-y: hidden;
}
.nav-scroller .nav {
	display: flex;
	flex-wrap: nowrap;
	padding-bottom: 1rem;
	margin-top: -1px;
	overflow-x: auto;
	text-align: center;
	white-space: nowrap;
	-webkit-overflow-scrolling: touch;
}

.card-img-right {
	height: 100%;
	border-radius: 0 3px 3px 0;
}

.flex-auto {
	flex: 0 0 auto;
}

.h-250 {
	height: 250px;
}

@media (min-width: 768px) {
	.h-md-250 {
		height: 250px;
	}
}
/* Pagination */
.blog-pagination {
	margin-bottom: 4rem;
}

.blog-pagination > .btn {
	border-radius: 2rem;
}

/*
 * Blog posts
 */
.blog-post {
	margin-bottom: 4rem;
}

.blog-post-title {
	margin-bottom: 0.25rem;
	font-size: 2.5rem;
}

.blog-post-meta {
	margin-bottom: 1.25rem;
	color: #727272;
}